ACJ","sourcesContent":["/* * {\n  border: 1px solid red;\n} */\n\nhtml {\n    box-sizing: border-box;\n}\n*, *:before, *:after {\n    box-sizing: inherit;\n}\n\n/* Text with this class cannot be selected by user */\n.noselect {\n    -webkit-touch-callout: none; /* iOS Safari */\n      -webkit-user-select: none; /* Safari */\n       -khtml-user-select: none; /* Konqueror HTML */\n         -moz-user-select: none; /* Old versions of Firefox */\n          -ms-user-select: none; /* Internet Explorer/Edge */\n              user-select: none; /* Non-prefixed version, currently\n                                    supported by Chrome, Edge, Opera and Firefox */\n}\n\nbody {\n    display: grid;\n    grid-template-columns: auto 3fr;\n    grid-template-rows: auto 1fr;\n    grid-template-areas: \n        \"header header\"\n        \"sidebar content\";\n    font-family: 'Roboto', sans-serif;\n    color: #1f1f2f;\n    height: 100vh;\n    margin: 0;\n}\n\na {\n    text-decoration: none;\n    color: inherit;\n}\n\n/* HEADER STARTS */\nheader {\n    grid-area: header;\n    font-size: 1.5rem;\n    color: white;\n    background-color: #db4c3f;\n    display: flex;\n    align-items: center;\n    gap: 0.5rem;\n    padding: 0.5rem 1rem;\n}\n\n#hide-menu-btn {\n    font-size: 1.5rem;\n    border: none;\n    color: white;\n    background-color: inherit;\n}\n\n#hide-menu-btn:hover {\n    cursor: pointer;\n    background-color: #e26f65;\n}\n/* HEADER ENDS */\n\nul {\n    list-style: none;\n    margin: 0;\n    padding: 0;\n}\n\n/* SIDEBAR STARTS */\n.sidebar {\n    grid-area: sidebar;\n    background-color: #fafafa;\n    width: 300px;\n    overflow-x: hidden;\n    position: absolute;\n    top: 2.75rem;\n    height: 100%;\n    z-index: 1;\n    transition: width 250ms cubic-bezier(0.4, 0, 0.2, 1) 0ms;\n}\n\n.sidebar.hidden {\n    width: 0;\n}\n\n/* Must create an inner container in sidebar otherwise\nthe padding is still displayed when sidebar is hidden */\n.sidebar-inner-container {\n    padding: 2rem 1rem;\n}\n\n.sidebar-tasks-item-link {\n    display: flex;\n    gap: 0.5rem;\n    padding: 0.5rem 1rem;\n    white-space: nowrap;\n}\n\n.sidebar-tasks-item-link:hover,\n.sidebar-projects-item-link:hover {\n    background-color: #eeeeee;\n    cursor: pointer;\n}\n\n.fa-inbox {\n    color: #4a87e4;\n}\n\n.fa-calendar-day {\n    color: #88c397;\n}\n\n.fa-calendar-week {\n    color: #8d62d0;\n}\n\n.sidebar-tasks-item-count {\n    margin-left: auto;\n    color: #aaaaaa;\n    font-size: 0.9rem;\n    align-self: center;\n}\n\n.sidebar-projects {\n    margin-top: 2rem;\n}\n\n.projects-title-container {\n    display: flex;\n    justify-content: space-between;\n    font-size: 1.15rem;\n}\n\n.projects-title-container,\n.hr-line {\n    padding: 0 1rem;\n}\n\n.projects-add-btn {\n    color: #8b8b8b;\n}\n\n.projects-add-btn:hover {\n    cursor: pointer;\n    color: #db4c3f;\n}\n\n.sidebar-projects-item-link {\n    display: flex;\n    justify-content: space-between;\n    padding: 0.5rem 0 0.5rem 1rem;\n    margin-left: 1rem;\n}\n\n.sidebar-projects-options {\n    position: relative;\n    font-size: 0.8rem;\n    padding: 0 1rem;\n}\n\n.dropdown-menu {\n    display: block;\n    position: absolute;\n    background-color: white;\n    border: 1px solid #dddddd;\n    top: 1.3rem;\n    right: -0.7rem;\n    overflow: auto;\n     box-shadow: 0 1px 8px 0 rgb(0 0 0 / 8%);\n    z-index: 1;\n}\n  \n.dropdown-menu div {\n    display: block;\n    padding: 0.5rem 1rem;\n}\n  \n.dropdown-menu div:hover {\n    background-color: #dddddd;\n}\n/* SIDEBAR ENDS */\n\n.content {\n    grid-area: content;\n    display: flex;\n    justify-content: center;\n    padding: 2rem 0;\n    transition: margin-left 250ms cubic-bezier(0.4, 0, 0.2, 1) 0ms,\n                background-color 250ms cubic-bezier(0.4, 0, 0.2, 1) 0ms;\n}\n\n.content.full-width {\n    transition: margin-left 250ms cubic-bezier(0.4, 0, 0.2, 1) 0ms;\n}\n\n/* Select .overlay only and not .overlay.off */\n[class=\"overlay\"] {\n    position: fixed;\n    background-color: rgba(0,0,0,.5);\n    height: 100%;\n    width: 100%;\n    top: 2.75rem;\n}\n\n.content-inner-container {\n    width: 70%;\n    display: flex;\n    flex-direction: column;\n    gap: 1rem;\n}\n\nh2 {\n    margin: 0;\n}\n\n.add-task-container {\n    color: #8b8b8b;\n    padding-top: 0.5rem;\n\n}\n\n.add-task-btn {\n    background: none;\n\tcolor: inherit;\n\tborder: none;\n\tpadding: 0;\n\tfont: inherit;\n\tcursor: pointer;\n\toutline: inherit;\n}\n\n.add-task-btn:hover {\n    color: #dd4b39;\n}\n\n.add-task-btn::before {\n    /* Add '+' before 'Add task' */\n    font-family: fontAwesome;\n    content: \"\\f055\\00a0\";\n    font-size: 1.25rem;\n    margin-right: 0.4rem;\n}\n\n.empty-state-container {\n    display: flex;\n    flex-direction: column;\n    align-items: center;\n}\n\n.empty-state-img {\n    background-image: url('./img/empty1.jpg');\n    background-position: center;\n    background-size: contain;\n    background-repeat: no-repeat;\n    width: 100%;\n    min-height: 200px;\n}\n\n.empty-state-header,\n.empty-state-txt {\n    text-align: center;\n}\n\n/* NEW TASK FORM STARTS */\n.task-form {\n    display: flex;\n    flex-direction: column;\n    gap: 0.5rem;\n    padding: 0.75rem 0;\n}\n\n.task-form-editor {\n    display: flex;\n    flex-direction: column;\n    gap: 0.5rem;\n    padding: 0.5rem 0.5rem;\n    border: 1px solid #e5e5e5;\n    border-radius: 10px;\n}\n\n.task-form-editor:focus-within {\n    border-color: #999;\n}\n\ninput[type=text],\ntextarea {\n    appearance: none;\n    border: 1px solid transparent;\n}\n  \ninput[type=text]:focus,\ntextarea:focus,\ninput[type=date]:focus {\n    outline: none;\n}\n\n#taskName {\n    font-size: 0.9rem;\n}\n\n#taskDescription {\n    resize: none;\n}\n\n#taskDescription,\n#taskDueDate {\n    font-family: \"Helvetica\", \"Arial\", sans-serif;\n}\n\n#taskDueDate {\n    color: #8b8b8b;\n    align-self: flex-start;\n    border: 1px solid #e5e5e5;\n    border-radius: 5px;\n    padding: 0.5rem 0.25rem;\n}\n\n#taskDueDate:focus {\n    color: #1f1f2f;\n}\n\n.form-btn-container {\n    display: flex;\n    justify-content: flex-end;\n    gap: 0.5rem;\n}\n\n.cancel-task-form-btn,\n.add-task-form-btn {\n    cursor: pointer;\n    font-weight: 600;\n    border: none;\n    border-radius: 5px;\n    padding: 8px 12px;\n}\n\n.cancel-task-form-btn {\n    background-color: #f5f5f5;\n}\n\n.cancel-task-form-btn:hover {\n    background-color: #e5e5e5;\n}\n\n.add-task-form-btn {\n    color: white;\n    background-color: #f1b7b2;\n}\n\n.add-task-form-btn:hover {\n    background-color: #db4c3f;\n}\n/* NEW TASK FORM ENDS */\n\n\n/* TASK LIST STARTS */\n.task-list-item-container {\n    display: grid;\n    justify-content: space-between;\n    grid-template-areas: \n        \"task taskOptions\"\n        \"taskDueDate taskDueDate\";\n    font-size: 0.9rem;\n    padding: 0.5rem 0;\n    border-bottom: 1px solid #e5e5e5;\n}\n\n/* Don't display task container as grid when editing a task */\n.task-list-item-container:has(.task-form) {\n    display: initial;\n}\n\n.task {\n    grid-area: task;\n}\n\n.task-label {\n    display: flex;\n    align-items: center;\n}\n\n/* Custom checkbox starts\nSource: https://www.sliderrevolution.com/resources/css-checkbox/ (task description added) */\n.task-label,\n.task-description {\n    -webkit-user-select: none;\n    user-select: none;\n    -webkit-tap-highlight-color: transparent;\n    cursor: pointer;\n}\n\n.task-label span,\n.task-description span {\n    display: inline-block;\n    vertical-align: middle;\n    transform: translate3d(0, 0, 0);\n}\n\n.task-label span:first-child {\n    position: relative;\n    width: 20px;\n    height: 20px;\n    /* Add min width to avoid circle being squashed on mobile when task name too long */\n    min-width: 20px;\n    border-radius: 50%;\n    transform: scale(1);\n    vertical-align: middle;\n    border: 1px solid #B9B8C3;\n    transition: all 0.2s ease;\n}\n\n.task-label span:first-child svg {\n    position: absolute;\n    z-index: 1;\n    top: 5px;\n    left: 3px;\n    fill: none;\n    stroke: white;\n    stroke-width: 2;\n    stroke-linecap: round;\n    stroke-linejoin: round;\n    stroke-dasharray: 16px;\n    stroke-dashoffset: 16px;\n    transition: all 0.3s ease;\n    transition-delay: 0.1s;\n    transform: translate3d(0, 0, 0);\n}\n\n.task-label span:first-child:before {\n    content: \"\";\n    width: 100%;\n    height: 100%;\n    background: lightgrey;\n    display: block;\n    transform: scale(0);\n    opacity: 1;\n    border-radius: 50%;\n    transition-delay: 0.2s;\n}\n\n.task-label span:last-child {\n    margin-left: 8px;\n}\n\n.task-label span:last-child:after,\n.task-description span:last-child:after {\n    content: \"\";\n    position: absolute;\n    top: 8px;\n    left: 0;\n    height: 1px;\n    width: 100%;\n    background: #B9B8C3;\n    transform-origin: 0 0;\n    transform: scaleX(0);\n}\n\n.task-label:hover span:first-child {\n    border-color: grey;\n}\n\n.task-checkbox:checked + .task-label span:first-child {\n    border-color: grey;\n    background: grey;\n    animation: check 0.6s ease;\n}\n\n.task-checkbox:checked + .task-label span:first-child svg {\n    stroke-dashoffset: 0;\n}\n\n.task-checkbox:checked + .task-label span:first-child:before {\n    transform: scale(2.2);\n    opacity: 0;\n    transition: all 0.6s ease;\n}\n\n.task-checkbox:checked + .task-label span:last-child,\n.task-checkbox:checked ~ .task-description span:last-child {\n    color: #B9B8C3;\n    transition: all 0.3s ease;\n}\n\n.task-checkbox:checked + .task-label span:last-child:after,\n.task-checkbox:checked ~ .task-description span:last-child:after {\n    transform: scaleX(1);\n    transition: all 0.3s ease;\n}\n\n@keyframes check {\n  50% {\n    transform: scale(1.2);\n  }\n}\n/* Custom checkbox ends */\n\n.task-description {\n    font-size: 0.8rem;\n    color: #8b8b8b;\n    margin-left: 1.8rem;\n}\n\n.task-due-date-container {\n    grid-area: taskDueDate;\n    display: flex;\n    align-items: center;\n    color: #8b8b8b;\n    gap: 0.3rem;\n    margin-left: 1.8rem;\n    margin-top: 0.5rem;\n}\n\n.task-due-date {\n    font-size: 0.8rem;\n}\n\n.task-option-container {\n    grid-area: taskOptions;\n    display: flex;\n    align-items: center;\n    gap: 1.25rem;\n    font-size: 1rem;\n    margin-left: 1.25rem;\n}\n\n.task-option-trashcan,\n.task-option-edit,\n.task-option-calendar {\n    cursor: pointer;\n    transition: all .07s ease;\n}\n\n.task-option-trashcan:hover,\n.task-option-edit:hover,\n.task-option-calendar:hover {\n    transform: scale(1.2);\n}\n/* TASK LIST ENDS */\n\n@media only screen and (min-width: 700px) {\n    .sidebar {\n        position: initial;\n    }\n\n    .content:not(.full-width) {\n        background-color: inherit;\n    }\n\n    [class=\"overlay\"] {\n        display: none;\n    }\n\n    .content-inner-container {\n        width: 50%;\n    }\n\n    .empty-state-img {\n        background-size: cover;\n        min-height: 400px;\n    }\n\n    .task-list-item-container {\n        grid-template-columns: 1fr auto auto;\n        grid-template-areas: \n            \"task taskDueDate taskOptions\";\n    }\n\n    .task-due-date-container {\n        color: inherit;\n        margin-left: 1.25rem;\n        margin-top: 0;\n    }\n}\n\n@media only screen and (min-width: 1400px) {\n    .empty-state-img {\n        background-size: contain;\n    }\n}"],"sourceRoot":""}]);
